import React, { useState } from "react";
import Navbar from "../../global/navbar";
import Footer from "../../global/footer";
import Menup from "../menu";
import Product from "./Product";

function Myproducts() {
  const [sec, setSec] = useState(0);
  return (
    <>
      <Navbar here={true} />
      <div className="containProfile">
        <div className="left">
          <Menup link={2} />
        </div>
        <div className="right">
          <div className="profileproduct">
            <h5 className={sec === 0 && "active"} onClick={() => setSec(0)}>
              Strategys
            </h5>
            <h5 className={sec === 1 && "active"} onClick={() => setSec(1)}>
              Indicators
            </h5>
            <h5 className={sec === 2 && "active"} onClick={() => setSec(2)}>
              Source
            </h5>
          </div>
          {sec === 0 && <Product type="Strategys" />}
          {sec === 1 && <Product type="Indicators" />}
          {sec === 2 && <Product type="Source" />}
        </div>
      </div>
      <Footer />
    </>
  );
}

export default Myproducts;
