import React, { useState, useEffect } from "react";
import { useSelector, useDispatch } from "react-redux";
import moment from "moment";
import io from "socket.io-client";
import axios from "axios";
import qs from "querystring";
import Signin from "../../signIn";
import { TextareaAutosize } from "@material-ui/core";
import RightArrow from "../../../asset/images/rightArrow";
import { AddImpro, AddQa } from "../../../Actions";

const socket = io();

function Addcom(props) {
  const dispatch = useDispatch();
  // const data = useSelector((state) => state.products);
  const login = useSelector((state) => state.login);
  const [com, setcom] = useState("");
  const [show, setShow] = useState(false);

  const submit = function () {
    if (!login) {
      setShow(true);
      return;
    }
    props.type === "qa"
      ? socket.emit("qa", { type: props.type, com, id: socket.id })
      : socket.emit("impro", { type: props.type, com, id: socket.id });

    const date = moment().format("YYYY-MM-DDTHH:mm:ss.SSSZ");
    props.type === "qa"
      ? dispatch(
          AddQa({
            id: props.id,
            data: {
              _id: date,
              question: com,
              answers: [],
              productId: props.id,
              timestamp: date,
            },
          })
        )
      : dispatch(
          AddImpro({
            id: props.id,
            data: {
              _id: date,
              improvement: com,
              answers: [],
              productId: props.id,
              minus: 0,
              plus: 0,
              timestamp: date,
            },
          })
        );
    props.change();
    setcom("");

    const endpoint =
      props.type === "qa"
        ? "/api/product/add/question"
        : "/api/product/add/impro";

    const data =
      props.type === "qa"
        ? qs.stringify({
            id: props.id,
            question: com,
          })
        : qs.stringify({
            id: props.id,
            impro: com,
          });

    axios
      .post(endpoint, data, {
        headers: {
          "Content-Type": "application/x-www-form-urlencoded",
        },
      })
      .then(function (response) {
        // handle success
        // console.log(response.data);
      })
      .catch(function (error) {
        // handle error
        console.log(error);
      })
      .then(function () {
        // always executed
      });
  };

  useEffect(() => {
    props.type === "qa"
      ? socket.emit("qa", { type: props.type, com, id: socket.id })
      : socket.emit("impro", { type: props.type, com, id: socket.id });
    // console.log(data);
    return () => {
      socket.emit("disconnect");
      socket.off();
    };
  }, [com]);

  const closesign = () => {
    setShow(false);
  };

  return (
    <>
      <div className="comment">
        <form>
          <TextareaAutosize
            aria-label="minimum height"
            rowsMin={1}
            value={com}
            onChange={(e) => setcom(e.target.value)}
            placeholder={props.placeholder}
          />
          <a onClick={submit}>
            <RightArrow />
          </a>
        </form>
      </div>

      {show ? <Signin close={closesign} show={show} /> : <></>}
    </>
  );
}

export default Addcom;
